  // Parses data to extract workable time value
  // Also contains a switch to return the min or max of the provided time depending on what is requested.
  parseTime(time, setting) {
    if (time.length === 4) {
      time = `0${time}`;
    }

    let hour = time.substring(0,2);
    let min = time.substring(3,5);

    if (setting === "d-min") {
        return this.getDefaultMin(hour, min);
    }

    if (setting === "d-max") {
        return this.getDefaultMax(hour, min);
    }

    if (setting === "check-in") {
        return this.getCheckInTime(hour, min);
    }
  }

  // Subtracts 10 minutes from a string, the JavaScript way
  getDefaultMin(hour, min) {
      const minMinutes = parseInt(this.state.minMinutes);
      const minHours = parseInt(this.state.minHours);
      if (parseInt(min) < minMinutes) {
          hour = parseInt(hour) - 1;
          min = parseInt(min) + (60 - minMinutes);
      } else {
          hour = parseInt(hour);
          min = parseInt(min) - minMinutes;
      }

      hour -= minHours;
      if (hour < 0) {
        hour += 24;
      }

      if (min < 10) {
          min = `0${min}`;
      }

        if (hour === -1) {
            hour = 23;
        }

        if (hour === 24) {
            hour = 0;
        }

        if (min === 60) {
            min = "00";
        }

      // console.log(time, "Stop Min - ",`${parseInt(hour)}:${min}`);
      return `${parseInt(hour)}:${min}`;
  }

  // Adds 10 minutes to a string, the JavaScript way
  getDefaultMax(hour, min) {
    const maxMinutes = parseInt(this.state.maxMinutes);
    const maxHours = parseInt(this.state.maxHours);
    if (parseInt(min) > (60 - maxMinutes)) {
        hour = parseInt(hour) + 1;
        min = parseInt(min) - (60 - maxMinutes);
    } else {
        hour = parseInt(hour);
        min = parseInt(min) + maxMinutes;
    }

    hour += maxHours;
    if (hour > 24) {
        hour -= 24;
    }

    if (min < 10) {
        min = `0${min}`;
    }

    if (hour === -1) {
        hour = 23;
    }

    if (hour === 24) {
        hour = 0;
    }

    if (min === 60) {
        hour = parseInt(hour) + 1;
        min = "00";
    }

    // console.log(time, "Stop Max - ",`${parseInt(hour)}:${min}`);
    return `${parseInt(hour)}:${min}`;
  }

  getCheckInTime(hour, min) {
    if (parseInt(min) < 45) {
        hour = parseInt(hour) - 1;
        min = 60 - (Math.abs(parseInt(min) - 45))
    } else {
        min = parseInt(min) - 45;
    }

    if (min < 10) {
        min = `0${min}`;
    }

    if (hour === -1) {
        hour = 23;
    }

    if (hour === 24) {
        hour = 0;
    }

      return `${parseInt(hour)}:${min}`;
  }

