  handleChange(e) {
    this.setState({
        [e.target.name]: e.target.value
    })
  }

  afrToggle(e) {
      e.preventDefault();
      const afrCheck = this.state.afrCheck;
      this.setState({
          afrCheck: !afrCheck,
      })
  }

  processCheck(from) {
      const minM = this.state.minMinutes;
      const minH = this.state.minHours;
      const maxM = this.state.maxMinutes;
      const maxH = this.state.maxHours;

      if (from === "minH") {
          if (isNaN(parseInt(minH)) || minH < 0 || minH > 23) {
              return true;
          }
      }
      if (from === "minM") {
          if (isNaN(parseInt(minM)) || minM < 0 || minM > 59) {
              return true;
          }
      }
      if (from === "maxH") {
          if (isNaN(parseInt(maxH)) || maxH < 0 || maxH > 23) {
              return true;
          }
      }
      if (from === "maxN") {
          if (isNaN(parseInt(maxM)) || maxM < 0 || maxM > 59) {
              return true;
          }
      }

      if (from === "button") {
          if (isNaN(parseInt(minM)) || isNaN(parseInt(minH)) || isNaN(parseInt(maxM)) || isNaN(parseInt(maxH))) {
              return true;
          }
    
          if (minM >= 0 && minM <= 59 && minH >= 0 && minH <= 23 && maxM >= 0 && maxM <= 59 && maxH >= 0 && maxH <= 23) {
              return false;
          } else {
              return true;
          }
      }
  }

