import React, { Component } from 'react';
import "../styles/Popup.css";
import { CSVLink } from "react-csv";
import GoogleLogo from "../images/google-logo.png";


class Popup extends Component {
    state = {
        data: [],
        name: "",
        address: "",
        city: "",
        state: "",
        zip: "",
        phone: "",
        cur: 0,
        selected: [],
        unselected: [],
        autoSelected: false,
        view: false,
        viewSeq: 0,
    }

    incrementCur(e, oper) {
        e.preventDefault();

        console.log(e);
        let cur = this.state.cur;

        if (oper === "+") {
            // for (let x = cur; x < this.props.adds.length - 1; x++) {
            //     if (this.props.adds[x + 1].length !== 1) {
            //         console.log(this.props.adds[x + 1]);
            //         cur = x;
            //         x = this.props.adds.length;
            //     }
            // }
            if (cur === this.props.adds.length - 1) {
                this.setState({
                    cur: -1,
                })
            } else {
                this.setState({
                    cur: cur + 1,
                })
            }
        }

        if (oper === "-") {
            if (cur === -1) {
                this.setState({
                    cur: this.props.adds.length - 1,
                })
            } else {
                this.setState({
                    cur: cur - 1,
                })
            }
        }

        if (oper === "--") {
                this.setState({
                    cur: 0,
                })
        }

        if (oper === "++") {
            this.setState({
                cur: this.props.adds.length - 1,
            })
        }
    }

    getPopup() {
        if (this.state.autoSelected === false) {
            this.autoSelect();
        }

        let selectedList;
        let list;
        const cur = this.state.cur;
        const data = this.props.adds[cur];
        const selected = this.state.selected;

        if (cur === -1) {
            list = this.state.data.map((info, i) => {
                return <div key={`${info.Name}-${i}`}>
                    <div onClick={e => this.toggleSelected(e, i)} className={`finish-list ${selected.includes(i) ? "selected" : "unselected"}`}>
                        <div className="top-row">
                            <p style={{fontSize: "20px"}}>{info.Name}</p>
                        </div>
                        <p className="barcode">{info.Barcode !== "" ? `Barcode: ${info.Barcode}` : info.Barcode}</p>
                        <p className="phone">{info.Phone !== "" ? `Phone: ${info.Phone}` : info.Phone}</p>
                        <div className="bottom-row">
                            <p>{info.Address}</p>
                            <p>{info.City}</p>
                            <p>{info.State}</p>
                            <p>{info.Zip}</p>
                        </div>
                    </div>
                    <button onClick={e => this.toggleView(e, i)} className="finish-view-btn">Edit</button>
                </div>
            })
            selectedList = (
                <div className="finish-container">
                    <div className="finish-btn-container">
                        <CSVLink data={this.compileData()} target="_blank" filename={"address-points-export.csv"} className="finish-btn">
                            Download .csv File
                        </CSVLink>
                    </div>
                    <div className="finish-lists-container">
                        {list}
                    </div>
                </div>
            )
        } else {
            if (this.state.data.length !== 0) {
                selectedList = data.map(info => {
                    const curData = this.state.data[info.seq];
                    return <div key={`${info.seq}-${curData.Name}`}>
                        <div className={this.state.selected.includes(info.seq) ? "list-container selected" : "list-container unselected"} onClick={e => this.toggleSelected(e, info.seq)}>
                            <div className="top-row">
                                <p style={{fontSize: "20px"}}>{curData.Name}</p>
                            </div>
                            <p className="barcode">{curData.Barcode !== "" ? `Barcode: ${curData.Barcode}` : curData.Barcode}</p>
                            <p className="phone">{curData.Phone !== "" ? `Phone: ${curData.Phone}` : curData.Phone}</p>
                            <div className="bottom-row">
                                <p>{this.getAddInfo(curData.Address, "num")}</p>
                                <p>{this.getAddInfo(curData.Address, "street")}</p>
                                <p>{curData.City}</p>
                                <p>{curData.State}</p>
                                <p>{curData.Zip}</p>
                            </div>
                        </div>
                        <button onClick={e => this.toggleView(e, info.seq)} className="view-btn">
                            <svg xmlns="http://www.w3.org/2000/svg" fill="white" viewBox='0 0 50 50' style={{width: "35px"}}><path d="M39.7 14.7 33.3 8.3 35.4 6.2Q36.25 5.35 37.525 5.375Q38.8 5.4 39.65 6.25L41.8 8.4Q42.65 9.25 42.65 10.5Q42.65 11.75 41.8 12.6ZM37.6 16.8 12.4 42H6V35.6L31.2 10.4Z"/></svg>
                        </button>
                    </div>
                })
            }
        }

        return (
            <div className='background-overlay'>
                {this.state.isLoading === true ? 
                <div className="popup-container">
                    <div className="loader-container">
                        <h2>Loading</h2>
                    </div>
                </div> : 
                <div className="popup-container">
                    {selectedList}
                    <div className="incr-container">
                        <button style={{marginRight: "0px"}} disabled={this.state.cur === 0} onClick={e => this.incrementCur(e, "--")} className="incr-btn dn-btn">{"<<"}</button>
                        <button disabled={this.state.cur === 0} onClick={e => this.incrementCur(e, "-")} className="incr-btn dn-btn">{"<"}</button>
                        <h4>{this.state.cur + 1} out of {this.props.adds.length}</h4>
                        <button disabled={this.state.cur === this.props.adds.length || this.state.cur === -1} onClick={e => this.incrementCur(e, "+")} className="incr-btn up-btn">{">"}</button>
                        <button style={{marginLeft: "0px"}} disabled={this.state.cur === this.props.adds.length || this.state.cur === -1} onClick={e => this.incrementCur(e, "++")} className="incr-btn up-btn">{">>"}</button>
                    </div>
                    <div className="sel-container">
                        <button className="sel-btn" onClick={e => this.selectAll(e)}>Select All</button>
                    </div>
                    <div className="progress-bar-back"></div>
                    <div className="progress-bar" style={this.getProgressBar()}></div>
                </div>}
            </div>
        )
    }

    getProgressBar() {
        return this.state.cur !== -1 ? {width: `${((this.state.cur + 1) / this.props.adds.length) * 1000}px`} : {width: '1000px'};
    }

    selectAll(e) {
        e.preventDefault();
        const cur = this.state.cur;
        const data = this.props.adds[cur];
        let selected = this.state.selected;

        if (cur !== -1) {
            for (let x = 0; x < data.length; x++) {
                console.log(data[x].seq, selected);
                if (!selected.includes(data[x].seq)) {
                    selected.push(data[x].seq);
                }
            }
    
            selected = this.sortList(selected);
            this.setState({
                selected: selected
            }, () => {
                this.incrementCur(e, "+");
            });
        } else {
            let newSel = [];
            for (let x = 0; x < this.state.data.length; x++) {
                newSel.push(x);
            }

            console.log(newSel);
            this.setState({
                selected: newSel
            })
        }
    }

    getAddInfo(add, info) {
        let num = parseInt(add).toString();

        if (info === "num") {
            return num;
        }

        if (info === "street") {
            let street = add.replace(num + " ","");
            return street;
        }
    }

    toggleSelected(e, seq) {
        e.preventDefault();
        let curSelected = this.state.selected;
        let curUnselected = this.state.unselected;

        if (curSelected.includes(seq)) {
            curSelected.splice(curSelected.findIndex(e => e === seq), 1);
            curUnselected.push(seq);
        } else {
            curSelected.push(seq);
            curUnselected.splice(curUnselected.findIndex(e => e === seq), 1);
        }

        curSelected = this.sortList(curSelected);
        curUnselected = this.sortList(curUnselected);

        this.setState({
            selected: curSelected,
            unselected: curUnselected
        })
    }

    sortList(list) {
        return list.sort(function(a, b){return a - b});
    }

    autoSelect() {
        const data = this.props.adds;
        let selected = [];
        let unselected = [];

        for (let x = 0; x < data.length; x++) {
            if (data[x].length === 1) {
                selected.push(data[x][0].seq);
            } else {
                for (let y = 0; y < data[x].length; y++) {
                    if (data[x][y].barcode !== "" || data[x][y].phone !== "") {
                        selected.push(data[x][y].seq);
                    } else {
                        unselected.push(data[x][y].seq);
                    }
                }
            }
        }

        this.setState({
            data: this.props.data,
            adds: this.props.adds,
            selected: selected,
            unselected: unselected,
            autoSelected: true
        })
    }

    compileData() {
        const selected = this.state.selected;
        let newData = [];
        for (let x = 0; x < selected.length; x++) {
            newData.push(this.state.data[selected[x]])
        }
        return newData;
    }

    checkLength(text) {
        let newText = text;

        if (text.length > 14) {
            newText = text.substring(0,13) + "...";
        }

        return newText;
    }

    toggleView(e, seq) {
        e.preventDefault();

        this.setState({
            view: !this.state.view,
            viewSeq: seq,
            name: this.state.data[seq].Name,
            address: this.state.data[seq].Address,
            city: this.state.data[seq].City,
            state: this.state.data[seq].State,
            zip: this.state.data[seq].Zip,
            phone: this.state.data[seq].Phone,
        },() => {
            console.log(this.state.viewSeq);
        })
    }

    handleChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    submitChanges(e, seq) {
        e.preventDefault();
        let data = this.state.data;
        // let curInfo = data[seq];
        const name = this.state.name;
        const address = this.state.address;
        const city = this.state.city;
        const state = this.state.state;
        const zip = this.state.zip;
        const phone = this.state.phone;

        data[seq].Name = name;
        data[seq].Address = address;
        data[seq].City = city;
        data[seq].State = state;
        data[seq].Zip = zip;
        data[seq].Phone = phone;

        this.setState({
            data: data
        }, () => {
            this.toggleView(e, seq);
            if (this.state.unselected.includes(seq)) {
                this.toggleSelected(e, seq);
            }
        })
    }

    getView() {
        const seq = this.state.viewSeq;
        const data = this.state.data[seq];

        return <div className="view-background">
            <div className="view-window">
                <h2>{data.Name}</h2>
                <div className="view-input-container">
                    <input name="name" onChange={e => this.handleChange(e)} defaultValue={data.Name} placeholder="Name"/>
                    <input name="address" onChange={e => this.handleChange(e)} defaultValue={data.Address} placeholder="Address"/>
                    <input name="city" onChange={e => this.handleChange(e)} defaultValue={data.City} placeholder="City"/>
                    <input name="state" onChange={e => this.handleChange(e)} defaultValue={data.State} placeholder="State"/>
                    <input name="zip" onChange={e => this.handleChange(e)} defaultValue={data.Zip} placeholder="Zip"/>
                    <input name="phone" onChange={e => this.handleChange(e)} defaultValue={data.Phone} placeholder="Phone"/>
                    <input readOnly name="fullAddress" style={{border: "1px solid #ccc", marginTop: "20px", background: "#f5f5f5", color: "#333"}} value={`${data.Address}, ${data.City}, ${data.State} ${data.Zip}`} />
                    <a href={this.getSearch(data.Address, data.City, data.State, data.Zip)} target="_blank" rel="noreferrer">
                        <img src={GoogleLogo} alt="Google" />
                    </a>
                </div>
                <div className="view-window-btn-container">
                    <button className="view-window-btn cancel-btn" style={{marginRight: "5px"}} onClick={e => this.toggleView(e, this.state.viewSeq)}>Cancel</button>
                    <button className="view-window-btn submit-btn" style={{marginLeft: "5px"}} onClick={e => this.submitChanges(e, this.state.viewSeq)}>Submit</button>
                </div>
            </div>
        </div>
    }

    getSearch(add, city, state, zip) {
        const url = `https://www.google.com/search?q=${add.replaceAll(" ","+")}%2C+${city.replaceAll(" ","+")}%2C+${state}+${zip}`;
        return url;
    }

    render() {
        return (
            <div>
                {this.props.adds.length > 0 ? this.getPopup() : <div></div>}
                {/* <button onClick={e => this.compileData(e)} className="finish-btn">Finish</button> */}
                {this.state.view === true ? this.getView() : <div></div>}
            </div>
        )
    }
}

export default Popup