import React, { Component } from 'react'
import StopAdjust from './StopAdjust';
// import List from './List';

class Converter extends Component {
    state = {
        routes: [],
        disabled: false,
    }

    convert(e) {
        e.preventDefault();
        const data = this.props.stopData;
        const routeData = this.props.routeData;

        console.log("Imported Stops Data:", data);
        console.log("Imported Routes Data:", routeData);

        let curSeq;
        let prevSeq = parseInt(data[0].Sequence);
        let curStopsList = [];
        let routesList = [];

        // Generates a list of JSON data that's easier to manipulate/read in JavaScript and the console
        // Go through each line in the table, combine stops into respective routes
        // routeDesc is for easier debugging and for organizational purposes
        for (let i = 0; i < data.length; i++) {
            curSeq = parseInt(data[i].Sequence);

            if (curStopsList[0] !== undefined && curStopsList[1] !== undefined) {
                if (curStopsList[0].Sequence === curStopsList[1].Sequence) {
                    console.log("Found one");
                    let singleStop = curStopsList.shift();
                    let stopAsList = [singleStop];
                    console.log(singleStop, curStopsList);

                    routesList.push({
                        routeDesc: singleStop["D E L E T E before Import-RouteDescription"],
                        stops: stopAsList
                    })
                }
            }
            
            if (curSeq < prevSeq) {
                routesList.push({
                    routeDesc: curStopsList[0]["D E L E T E before Import-RouteDescription"],
                    stops: curStopsList,
                })
                

                curStopsList = [];
            }

            curStopsList.push(data[i]);

            prevSeq = curSeq;
        }

        routesList.push({
            routeDesc: curStopsList[0]["D E L E T E before Import-RouteDescription"],
            stops: curStopsList,
        })

        this.setState({
            routes: routesList
        },() => {
            this.convertRouteData(routeData);
        })
    }

    convertRouteData(data) {
        console.log("Old Data:", data);
        console.log("Converted Data:", this.state.routes);
        let routes = this.state.routes;

        if (data.length === routes.length) {
            for (let i = 0; i < data.length; i++) {
                routes[i] = ({
                    routeDesc: routes[i].routeDesc,
                    routeInfo: data[i],
                    stops: routes[i].stops,
                })
            }
        }
    }

    render() {
        return (
            <div>
                <button onClick={e => this.convert(e)} style={{marginLeft: "20px"}} disabled={this.state.routes.length !== 0}>Convert .csv File to JSON</button>
                <StopAdjust data={this.state.routes}/>
            </div>
        )
    }
}

export default Converter