import React, { Component } from 'react'
import { CSVLink } from 'react-csv';

class RateAdjust extends Component {
    state = {
        routes: [],
        rows: [
            ['BillingGroup','GroupID','RouteID','Sequence','OverRideCommission','Name','Address','Address2','City','State','Zip','Plus4',	'Phone','Attn',	'LongRemarks','ShortRemarks','StopType','Pieces','Weight','ParcelType','Reference1','Reference2','RatePerOccurrence','SettlementPerOccurrence','Notes','StopTimeMin','StopTimeMax','StopTimePreferred','ChildOf','Description','RateMultiplier','Depot','Lat','Lon','RouteSurchargeAdjustment','CreatedBy','CreatedWhen','UpdatedBy','UpdatedWhen','UserField1','UserField2','UserField3','UserField4','UserField5','UserField6','ParentStopID','PostActive','ETA','MinOffset','MaxOffset','PreferredOffset','LoadStop','GlobalLoadStop','XMobileSecondaryLookup','CustomerRouteID','CustomerStopID','CustomerAccountID','IsParent','ParentStop','Sun','Mon','Tue','Wed','Thu','Fri','Sat','StartDate','EndDate','D E L E T E before Import-ECTimes','D E L E T E before Import-RouteDescription','D E L E T E before Import-Auth','D E L E T E before Import-Caller',]
        ],
        firstID: "",
        lastID: "",
        disabled: false,
    }

    convert(e) {
        e.preventDefault();
        const data = this.props.stopData;

        let curSeq;
        let prevSeq = parseInt(data[0].Sequence);
        let curStopsList = [];
        let routesList = [];

        // Generates a list of JSON data that's easier to manipulate/read in JavaScript and the console
        // Go through each line in the table, combine stops into respective routes
        // routeDesc is for easier debugging and for organizational purposes
        for (let i = 0; i < data.length; i++) {
            curSeq = parseInt(data[i].Sequence);

            if (curStopsList[0] !== undefined && curStopsList[1] !== undefined) {
                if (curStopsList[0].Sequence === curStopsList[1].Sequence) {
                    let singleStop = curStopsList.shift();
                    let stopAsList = [singleStop];

                    routesList.push({
                        stops: stopAsList
                    })
                }
            }
            
            if (curSeq < prevSeq) {
                routesList.push({
                    stops: curStopsList,
                })
                

                curStopsList = [];
            }

            curStopsList.push(data[i]);

            prevSeq = curSeq;
        }

        routesList.push({
            stops: curStopsList,
        })

        this.setState({
            routes: routesList
        },() => {
            this.rateAdjustment();
        });
    }

    rateAdjustment() {
        let routes = this.state.routes;
        let curStop;

        for (let x = 0; x < routes.length; x++) {
            for (let y = 0; y < routes[x].stops.length; y++) {
                curStop = routes[x].stops[y];
                curStop.Sequence = y.toString();

                if (curStop.XMobileSecondaryLookup !== undefined) {
                    if (!curStop.XMobileSecondaryLookup.includes("*") && !curStop.XMobileSecondaryLookup.includes("#") && curStop.RatePerOccurrence !== "0") {
                        routes[x].stops[y].RatePerOccurrence = "7.46"
                    } else {
                        routes[x].stops[y].RatePerOccurrence = "0"
                    }
                }
            }
        }

        console.log(routes);
        this.setState({
            routes: routes,
        },() => {
            this.generateCsv();
        })
    }

    generateCsv() {
        const data = this.state.routes;
  
        let rows = this.state.rows;
        let curRow = [];
  
        for (let x = 0; x < data.length; x++) {
            for (let y = 0; y < data[x].stops.length; y++) {
                let curStop = data[x].stops[y];
                const c = curStop;
                curRow = [c.BillingGroup, c.GroupID, c.RouteID, c.Sequence, c.OverRideCommission, c.Name, c.Address, c.Address2, c.City, c.State, c.Zip, c.Plus4, c.Phone, c.Attn, c.LongRemarks, c.ShortRemarks, c.StopType, c.Pieces, c.Weight, c.ParcelType, c.Reference1, c.Reference2, c.RatePerOccurrence, c.SettlementPerOccurrence, c.Notes, c.StopTimeMin, c.StopTimeMax, c.StopTimePreferred, c.ChildOf, c.Description, c.RateMultiplier, c.Depot, c.Lat, c.Lon, c.RouteSurchargeAdjustment, c.CreatedBy, c.CreatedWhen, c.UpdatedBy, c.UpdatedWhen, c.UserField1, c.UserField2, c.UserField3, c.UserField4, c.UserField5, c.UserField6, c.ParentStopID, c.PostActive, c.ETA, c.MinOffset, c.MaxOffset, c.PreferredOffset, c.LoadStop, c.GlobalLoadStop, c.XMobileSecondaryLookup, c.CustomerRouteID, c.CustomerStopID, c.CustomerAccountID, c.IsParent, c.ParentStop, c.Sun, c.Mon, c.Tue, c.Wed, c.Thu, c.Fri, c.Sat, c.StartDate, c.EndDate];
                rows.push(curRow);
            }
        }
  
        this.setState({
            rows: rows,
            firstID: rows[1][2],
            lastID: rows[rows.length - 1][2],
        },() => {
            console.log(this.state.rows);
        })
    }

    render() {
        return (
            <div>
                <button onClick={e => this.convert(e)} style={{marginLeft: "20px"}} disabled={this.state.routes.length !== 0}>Convert .csv File to JSON</button>
                <br />
                <br />
                <CSVLink data={this.state.rows} target="_blank" filename={`${this.state.firstID}-${this.state.lastID} stops-export.csv`} className={this.state.rows.length > 1 ? "download-btn" : "download-btn hidden"}>
                    Download .csv File
                </CSVLink>
            </div>
        )
    }
}

export default RateAdjust