import React, { Component } from 'react'
import Popup from './APsPopup';
// import List from './List';
// import TollCalc from './TollCalc';

class Converter extends Component {
    state = {
        disabled: false,
        addresses: [],
    }

    convertOrders(e) {
        e.preventDefault();
        let data = this.cleanData(this.props.data);

        let curAdd;
        let nextAdd;
        let addList = [];
        let curList = [];

        for (let x = 0; x < data.length; x++) {
            curAdd = {
                seq: x,
                address: {
                    num: this.getAddInfo(data[x].Address, "num"),
                    street: this.getAddInfo(data[x].Address, "street"),
                },
                city: data[x].City,
                name: data[x].Name,
                state: data[x].State,
                zip: data[x].Zip,
                barcode: data[x].Barcode,
                phone: data[x].Phone
            }

            curList.push(curAdd);

            if (x < data.length - 1) {
                nextAdd = {
                    seq: x + 1,
                    address: {
                        num: this.getAddInfo(data[x + 1].Address, "num"),
                        street: this.getAddInfo(data[x + 1].Address, "street"),
                    },
                    city: data[x + 1].City,
                    name: data[x + 1].Name,
                    state: data[x + 1].State,
                    zip: data[x + 1].Zip,
                    barcode: data[x + 1].Barcode,
                    phone: data[x + 1].Phone
                }
    
    
                if (curAdd.address.num !== nextAdd.address.num || curAdd.address.street !== nextAdd.address.street) {
                    addList.push(curList);
                    curList = [];
                }
            } else {
                addList.push(curList);
                curList = [];
            }
        }

        this.setState({
            addresses: addList
        }, () => {
            console.log(this.state.addresses);
        })
    }

    cleanData(data) {
        for (let x = 0; x < data.length; x++) {
            data[x].Name = this.toProperCase(data[x].Name);
            data[x].Address = this.toProperCase(data[x].Address.replaceAll(".",""));
            data[x].City = this.toProperCase(data[x].City);
            data[x].Zip = data[x].Zip.length === 5 ? data[x].Zip.toString() : "0" + data[x].Zip.toString();
            data[x].Phone = this.cleanPhoneNumber(data[x].Phone);
        }

        console.log(data);
        return data;
    }

    toProperCase(str) {
        return str.replace(/\w\S*/g, function(txt){return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();});
    }

    cleanPhoneNumber(num) {
        let newNum;

        if (num !== "") {
            num = num.replaceAll("-","")
                .replaceAll(".","")
                .replaceAll(" ","")
                .replaceAll("(","")
                .replaceAll(")","")
                .replaceAll("/","");
            
            if (num.length === 10) {
                newNum = num.substr(0, 3) + "-" + num.substr(3, 3) + "-" + num.substr(6, 4);
            } else if (num.length === 20) {
                newNum = num.substr(0, 3) + "-" + num.substr(3, 3) + "-" + num.substr(6, 4) + " / " + num.substr(10, 3) + "-" + num.substr(13, 3) + "-" + num.substr(16, 4)
            } else if (num.length === 11 && num.charAt(0) === "1") {
                newNum = num.charAt(0) + "-" + num.substr(1, 3) + "-" + num.substr(4, 3) + "-" + num.substr(7, 4);
            } else {
                newNum = num;
            }
        } else {
            newNum = "";
        }

        return newNum;
    }

    getAddInfo(add, info) {
        let num = parseInt(add).toString();

        if (info === "num") {
            return num;
        }

        if (info === "street") {
            let street = add.replace(num + " ","");
            return street;
        }
    }

    render() {
        return (
            <div>
                <button onClick={e => this.convertOrders(e)} style={{marginLeft: "70px"}} disabled={this.state.addresses.length !== 0}>Import .csv Into Table</button>
                {/* <TollCalc data={this.state.orders}/> */}
                {/* <List data={this.state.orders}/> */}
                <Popup adds={this.state.addresses} data={this.props.data}/>
            </div>
        )
    }
}

export default Converter
